@import url(https://fonts.googleapis.com/css2?family=Jost:wght@400;500&family=Rowdies&display=swap&family=Inconsolata);
* {
  margin: 0;
  font-family: "Jost", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
}

code {
  font-family: proxima-nova, Helvetica, Arial, sans-serif;
}

body {
  width: 100%;
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
}

a {
  transition: all 0.2s ease-in-out;
  text-decoration: none;
  font-weight: 700;
  color: inherit;
}

@media screen and (min-width: 601px) {
  /* body {
    padding: 2.5em;
  } */
}


.Project-container {
  padding: 90px 0 380px 0;
}

.Project-container:last-of-type {
  margin-bottom: 100px;
}

.Project-img-container {
  position: absolute;
}

.Project-img {
  width: 450px;
  position: absolute;
  border-radius: 4px;
  box-shadow: 0 10px 25px 2px grey;
}

.Project-img--left {
  left: 520px;
}

.Project-board {
  margin-top: 50px;
  width: 375px;
  padding: 25px 0;
  font-size: 15px;
}

.Project-board--left {
  position: absolute;
  text-align: justify;
}

.Project-board--right {
  position: absolute;
  text-align: justify;
  left: 850px;
}

.Project-text {
  padding: 10px 0;
}

.Project-header {
  font-size: 20px;
  font-family: "Rowdies", cursive;
}

.Project-icon {
  margin-top: 15px;
  cursor: pointer;
}

.Project-icon:hover {
  cursor: pointer;
}

.Language-list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  padding: 0px;
  margin: 15px 0 10px;
}

.Language-list-elem {
  margin: 3px 10px 3px 0;
  font-family: Inconsolata, monospace;
  font-size: 0.75rem;
  font-weight: 600;
  padding: 2px 10px;
  border-radius: 3px;
  color: #007bff;
  border: 1px solid #007bff;
  background: 0 0;
}

.Header-text {
  font-size: 30px;
  font-family: "Rowdies", cursive;
}

.Header-container {
  top: 25px;
  left: 36vw;
  text-align: center;
  padding: 30px 0 30px 0;
}

.Project-all-container {
  display: block;
  padding: 0 18%;
  margin: 0;
}

.Girl-container {
  position: relative;
}

.Girl-diva {
  height: 80vh;
  padding: 5% 0 10% 25%;
  z-index: 20;
}

.DisplayText {
  font-size: xx-large;
  font-family: "Rowdies", cursive;
}

.Welcome-container {
  position: absolute;
  left: 45%;
  top: 35%;
  pointer-events: none;
  color: #000;
  letter-spacing: 2px;
  line-height: 2;
}

.Footer {
  line-height: 1;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  height: auto;
  min-height: 70px;
  padding: 15px;
}

.Footer-credit {
  font-weight: 600;
  letter-spacing: 0.025em;
  font-family: Inconsolata, monospace;
  padding: 10px;
  text-decoration: none;
}
.Footer-credit:visited {
  color: rgb(168, 178, 209);
}

.Experience-container {
}

.Writing-page-content {
  display: block;
}

.Writing-archive {
  overflow: hidden;
  display: flex;
  padding: 20px 0 30px 0;
}

.Writing-archive div {
  position: absolute;
  width: 20%;
  right: -100%;
  justify-content: space-between;
  -webkit-animation: marquee 50s linear 0s infinite;
          animation: marquee 50s linear 0s infinite;
}

.Writing-archive .line-0 {
  -webkit-animation: marquee 50s linear 0s infinite;
          animation: marquee 50s linear 0s infinite;
}

.Writing-archive .line-1 {
  -webkit-animation: marquee 50s linear -5s infinite;
          animation: marquee 50s linear -5s infinite;
}

.Writing-archive .line-2 {
  -webkit-animation: marquee 50s linear -10s infinite;
          animation: marquee 50s linear -10s infinite;
}

.Writing-archive .line-3 {
  -webkit-animation: marquee 50s linear -15s infinite;
          animation: marquee 50s linear -15s infinite;
}

.Writing-archive .line-4 {
  -webkit-animation: marquee 50s linear -20s infinite;
          animation: marquee 50s linear -20s infinite;
}

.Writing-archive .line-5 {
  -webkit-animation: marquee 50s linear -25s infinite;
          animation: marquee 50s linear -25s infinite;
}

.Writing-archive .line-6 {
  -webkit-animation: marquee 50s linear -30s infinite;
          animation: marquee 50s linear -30s infinite;
}

.Writing-archive .line-7 {
  -webkit-animation: marquee 50s linear -35s infinite;
          animation: marquee 50s linear -35s infinite;
}

.Writing-archive .line-8 {
  -webkit-animation: marquee 50s linear -40s infinite;
          animation: marquee 50s linear -40s infinite;
}

.Writing-archive .line-9 {
  -webkit-animation: marquee 50s linear -45s infinite;
          animation: marquee 50s linear -45s infinite;
}

.Writing-archive:hover div {
  cursor: hover;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
  z-index: 1000;
}

@-webkit-keyframes marquee {
  0% {
    right: -100%;
  }
  100% {
    right: 100%;
  }
}

@keyframes marquee {
  0% {
    right: -100%;
  }
  100% {
    right: 100%;
  }
}

.Writing-line--title {
  font-weight: 500;
  text-decoration: none;
  font-size: 1rem;
  color: inherit;
}

.Writing-line-container {
  padding-right: 50px;
}

.Writing-line--publish {
  color: rgb(168, 178, 209);
  font-size: 0.9rem;
  margin: 5px 0;
}

.Writing-page-container {
  margin: auto;
  width: 100%;
  display: block;
}

.Writing-thought-container {
  position: absolute;
  left: 250px;
}

.Writing-thought-header {
  font-size: 28px;
  font-family: "Rowdies", cursive;
  margin-bottom: 10px;
}

.DesignNotes--thoughts {
  width: 100%;
  max-width: 300px;

  color: #31708f;
  background-color: #d9edf7;
  border-color: #bce8f1;

  margin: 0 0 0 20%;
  padding: 15px 5%;
  border: 1px solid transparent;
  border-radius: 4px;
}

.DesignNotes--thoughts p {
  font-size: 15px;
}

.DesignNotes--thoughts a {
  color: inherit;
}

.DesignNotes-page {
  padding-top: 100px;
  position: relative;
  margin: 0 auto;
}

.DesignNotes-container {
  display: flex;
  flex-wrap: wrap;
}

.Girl-dozing {
  height: 50vh;
}

.Girl-dozing-container {
  padding: 20px 40px;
}

.Top-button {
  display: inline-block;
  transition: all 0.2s ease-in-out;
  background: 0 0;
  border: none;
  outline: 0;
  width: 40px;
  position: fixed;
  bottom: 25px;
  right: 30px;
  cursor: pointer;
  z-index: 3;
}

.Top-button img {
  width: 100%;
}

@media screen and (max-width: 601px) {
  .Top-button {
    bottom: 15px;
    right: 3px;
    width: 35px;
  }

  .Top-button img {
    width: 40px;
  }
}

